import { getDefaultOptions, request } from 'api/helpers';

const resource = 'polygons';

export const apiPolygonGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiPolygonPost = async (serviceUrl, polygon) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: polygon ? JSON.stringify(polygon) : null,
  };
  return request(url, options);
};

export const apiPolygonPut = async (serviceUrl, polygon) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: polygon ? JSON.stringify(polygon) : null,
  };
  return request(url, options);
};

export const apiPolygonDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
