import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  name: PropTypes.string,
  numberOfSides: PropTypes.number,
  regular: PropTypes.number,
});

export const formValues = PropTypes.shape({
  name: PropTypes.string,
  numberOfSides: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  regular: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
});

export const formTouched = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  numberOfSides: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  regular: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  numberOfSides: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  regular: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
